body {
  margin: 0;
  padding: 0;
   font-family:'"Roboto", "Helvetica", "Arial", "sans-serif"';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
   height: 100%;
 

}
html{
  height: 100%;
  overflow-y: scroll;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#root{
	min-height:100%;
}

.place-details-info-container{
	width:90%;
	display:-webkit-flex;
	display:flex;
	-webkit-flex-direction: row;
	        flex-direction: row;
	margin-top:10px;
	box-sizing: border-box;
	border:40px solid #f2f2f2;

}



.weekdays-add-phone-and-rating-container{
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

.weekdays-container{
	display:-webkit-flex;
	display:flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex:1 1;
	        flex:1 1;
	-webkit-flex-direction: row;
	        flex-direction: row;
}

.status-color-open{
	color:green;
}
.status-color-closed{
	color:red;
}
.place-details-boxes{
	-webkit-flex:1 1;
	        flex:1 1;
	padding:60px 10px 20px 10px;
	background-color: #fbfbfb;
	box-sizing: border-box;
	padding:20px;

}

.wesite-navigation-button{
	text-decoration: none;
	border-radius: 5px;
	padding:11px;
	border: 1px solid blue;
	margin:5px;
	font-size: 14px;
}
.place-icons{
	display: -webkit-flex;
	display: flex;
	padding:0px;
	margin:0px;
	font-size:0.8rem;
	-webkit-align-items: center;
	        align-items: center;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
	
}
@media screen and (max-width:1030px) {
	.place-details-info-container{
		-webkit-flex-direction: column;
		        flex-direction: column;
		width:100%;
		border:none;
	  }
	  .place-details-boxes{
	   padding:10px;
	}
	.weekdays-add-phone-and-rating-container{
		-webkit-flex-direction:column;
		        flex-direction:column;
	}
	.weekdays-container{
		-webkit-justify-content: flex-start;
		        justify-content: flex-start;
	}
}

#map {
        height: 425px;
      }
      
      #right-panel {
        font-family: 'Roboto','sans-serif';
        line-height: 30px;
      
      }

    


      #right-panel i {
        font-size: 12px;
      }
      #right-panel {
        height: 500px;
        float: right;
        width: 100%;
        overflow: scroll;
        margin-bottom:10px;
      }
      #map {
        width:100%;
        margin-bottom:10px;
      }
    
      @media print {
        #map {
          height: 500px;
          margin: 0;
        }
    
      }
.formInputs{
	display:-webkit-flex;
	display:flex;
	-webkit-align-items:center;
	        align-items:center;
	-webkit-justify-content:center;
	        justify-content:center;
	-webkit-flex-direction:column;
	        flex-direction:column;
	width:50%;
	padding:20px 20px 0px 20px;
	-webkit-align-content: space-around;
	        align-content: space-around;
  /*background-color: red;*/
	


}
label,span, input {
	margin-bottom:10px;


}
.reset-and-submit-button-container{
  display:-webkit-flex;
  display:flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  /*background-color: green; */
  width:100%;
}
.form-container{

	width:100%;
	display:-webkit-flex;
	display:flex;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}

.checkboxes{
  border-radius:50%;
  border: 1px solid green;
}
.input-box{
	width:70%;
	font-size:1rem;
  padding:8px;
  height: 30px;
  letter-spacing: 1px;
  font-weight: 500;
  color: #000;
  border: none;
  border-radius: 20px;
  box-shadow: 0px 8px 15px #ccc;
  cursor: pointer;
  outline: none;



}
.submit-button{
  margin-top: 10px;
   font-size:1rem;
   padding:8px;
   width:30%;
  height: 45px;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  letter-spacing: 2.5px;
  font-weight: 500;
  color: #000;
  background-color: #85bf85;
  border: none;
  border-radius: 10px;
  box-shadow: 0px 8px 15px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease 0s;
  cursor: pointer;
  outline: none;




}
.hotel-container{
	width:80%;
	display:-webkit-flex;
	display:flex;
	-webkit-flex-direction:row;
	        flex-direction:row;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-flex-wrap: wrap;
	        flex-wrap: wrap;
}
.submit-button:hover {
  background-color: green;
  box-shadow: 0px 15px 20px rgba(46, 229, 157, 0.4);
  color: #fff;
  
}

/* checkbox css start from here*/

.checkbox-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  font-size: 1.1rem;

}



/* checkbox css ends here*/


